import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';



//! props means properties .... these are the read only sets of attributes for a component
export default function Navbar(props) {
  return (
    <>
    <nav  className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div  className="container-fluid">
    <Link  className="navbar-brand " to="">{props.title}</Link>
    <button  className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span  className="navbar-toggler-icon"></span>
    </button>
    <div  className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul  className="navbar-nav me-auto mb-2 mx-3 mb-lg-0" >
        <li  className="nav-item">
          <Link className="nav-link " aria-current="page" to="/">Home</Link>
        </li>
        <li  className="nav-item">
          <Link  className="nav-link " to="/about">{props.about}</Link>
        </li>

        <li>


      <div className="form-check form-switch me-1 my-2 ">
  <input className="form-check-input" onClick={props.toggleColor} type="checkbox" role="switch" aria-checked id="flexSwitchCheckDefault"/>
  <label className={`form-check-label text-${props.mode==='dark'?'light':'dark'}`} htmlFor="flexSwitchCheckDefault" >Enable Dark Mood</label>
</div>
        </li>
        
      </ul>
      <form  className="d-flex ">
        <input  className="form-control me-3 mx-2" type="search" placeholder="Search" aria-label="Search" />
        <button  className="btn btn-outline-success" type="submit">Search</button>
      </form>
    </div>
  </div>
</nav>

    </>
  );

  
}
//! is required means the tilte is zaroori , agar nhi dalo ghy tu console me eroor aye gha ,,
//! default hamesha likhna , ab yaha me agr title props pass na bhi kron to default props lag jai gha 
Navbar.propTypes={
    title: PropTypes.string.isRequired,
    about: PropTypes.string

}
Navbar.defaultProps={
    title:"set title here ",
    about:"navitem here"
}